import { useEffect, useState } from "react";
import "./App.css";
import Footer from "./components/Footer";
import Nav from "./components/Nav";
import NavMobile from "./components/NavMobile";
import Response from "./components/response";

function App() {
  const [show, setShow] = useState(false);
  const [names, setNames] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(
          "https://detectives-5126dc7943c5.herokuapp.com/",
          {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        const data = await response.json();
        setNames(data);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);
  const check = (type) => {
    let checkbox = document.querySelectorAll("[type=checkbox]");
    let checkboxArray = Array.from(checkbox);
    if (type === "radio") {
      let correct = 2;
      for (let i = 0; i < checkboxArray.length; i++) {
        if (checkboxArray[i].checked === true) {
          if (checkboxArray[i].name === "__39") {
            correct += 1;
          }
        }
      }
      answerRadio(correct);
    } else {
      let correct = 0;
      for (let i = 0; i < checkboxArray.length; i++) {
        if (checkboxArray[i].checked === true) {
          if (
            checkboxArray[i].name === "_2" ||
            checkboxArray[i].name === "_6"
          ) {
            correct += 1;
          }
        }
      }
      answer(correct);
    }
  };

  const answer = (correct) => {
    if (correct === 0) {
      document.querySelector(".an0").style.display = "block";
    } else if (correct === 1) {
      document.querySelector(".an1").style.display = "block";
    } else if (correct === 2) {
      setShow(true);
    }
  };
  const answerRadio = (correct) => {
    if (correct === 2) {
      document.querySelector(".an4").style.display = "block";
    } else if (correct === 3) {
      document.querySelector(".an2").style.display = "block";
      document.querySelector(".an0").style.display = "none";
    }
  };

  const times = () => {
    let checkbox = document.querySelectorAll("[type=checkbox]");
    let checkboxArray = Array.from(checkbox);
    let selected = 0;
    for (let i = 0; i < checkboxArray.length; i++) {
      if (checkboxArray[i].checked === true) {
        selected += 1;
      }
    }
    if (selected === 2 && show === false) {
      document.querySelector("#submit").addEventListener("click", () => {
        check("normal");
      });
    } else if (selected === 1 && show === true) {
      document.querySelector("#submit").addEventListener("click", () => {
        check("radio");
      });
    }
  };

  useEffect(() => times());
  const documents = [
    "مقالة جريدة",
    "صورة الضحية راجا باهي",
    "تقرير الشرطة",
    "صورة المشتبه بها زياد عامر",
    "ورقة تهديد",
    "صورة موقع الحادث",
    "استجواب زياد عامر رافي",
    "افادة الضابط فاروق",
    "صورة الضابط فاروق",
    "افادة أمريتا باها",
    "صورة الضابط فاروق",
  ];

  const rows = [
    "001",
    "002",
    "003",
    "004",
    "005",
    "006",
    "007",
    "008",
    "009",
    "010",
    "011",
    "012",
    "013",
    "014",
    "015",
    "016",
    "017",
    "018",
    "019",
    "020",
    "021",
    "022",
    "023",
    "024",
    "025",
    "026",
    "027",
    "028",
    "029",
    "030",
    "031",
    "032",
    "033",
    "034",
    "035",
    "036",
    "037",
    "038",
    "039",
    "040",
    "041",
    "042",
    "043",
    "044",
    "045",
    "046",
    "047",
    "048",
    "049",
    "050",
    "051",
  ];
  return (
    <main className="App">
      <Nav />
      <NavMobile />
      <div className="banner_wrapper">
        <div className="banner">
          <div
            className="marquee_wrapper"
            style={{ animationDuration: `${names.length * 2}s` }}
          >
            <div className="marquee">
              <span className="dot"></span>
              <span className="master">Master Detectives</span>
              <span className="dot"></span>
              {names.map((data, index) => (
                <>
                  <span key={index}> {data.name} </span>
                  <span className="dot"></span>
                  <span className="master">Master Detectives</span>
                  <span className="dot"></span>
                </>
              ))}
            </div>
          </div>
        </div>
      </div>
      <div className="header">
        <h1> لا أحد يعلم</h1>
      </div>
      {show === false ? (
        <div className="welcome">
          <h2 style={{ color: "#c22735" }}>مرحباً أيها المحقق</h2>
          <p> اثبت ان زياد كذب بشأن الحادث</p>
          <p>ما الوثيقتان التي تثبت أن الحادث لم يكن حادثًا؟</p>
        </div>
      ) : (
        <div className="welcome">
          <h2 style={{ color: "#c22735" }}>مرحباً أيها المحقق</h2>
          <p>أي سطر من الاستجواب يتضمن كذبة زياد؟</p>
        </div>
      )}

      <div className="main_container">
        <div className="documents">
          {show === false
            ? documents.map((doc, id) => (
                <label key={`___${id}`}>
                  <input
                    className="checkbox"
                    type="checkbox"
                    name={`_${id}`}
                    key={id}
                    onClick={(e) => {
                      e.target.parentNode.classList.toggle("red");
                      times();
                    }}
                  />
                  <span
                    key={`__${id}`}
                    className="checkmark"
                    id={`_${id}`}
                  ></span>
                  <span className="selection">{doc}</span>
                </label>
              ))
            : rows.map((row, id) => (
                <label key={`__${id}`}>
                  <input
                    className="checkbox"
                    type="checkbox"
                    name={`__${id}`}
                    key={id}
                    onClick={(e) => {
                      e.target.parentNode.classList.toggle("red");
                      times();
                    }}
                  />
                  <span
                    key={`__${id}`}
                    className="checkmark"
                    id={`_${id}`}
                  ></span>
                  <span className="selection">{row}</span>
                </label>
              ))}
        </div>
        <div className="btns_container">
          <input id="submit" type="button" value="تحقق" className="submit" />

          <input
            type="button"
            value="تلميح"
            className="submit hint_home"
            onClick={() => {
              document.querySelector(".answer").style.display = "none";
              document.querySelector(".an0").style.display = "none";
              document.querySelector(".hint1").style.display = "block";
            }}
          />
        </div>
      </div>
      <Footer />

      <Response />
    </main>
  );
}

export default App;
